<template>
  <v-app>
    <div id="nmForm" class="container">
      <div class="row m-3 component-background">
        <div>
          <div id="formHeading">
            <h2>{{ $t("formHeading") }}</h2>
          </div>
          <v-form id="form" ref="form" v-model="valid" :lazy-validation="lazy">
            <v-card-text>
              <v-container>
                <v-row>
                  <v-col cols="12" sm="6">
                    <v-text-field
                      id="name"
                      :label="formLabels('name')"
                      v-model="name"
                      :rules="nameRules"
                      filled
                      rounded
                      dense
                      required
                      color="rgb(123, 204, 231)"
                    ></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="6">
                    <v-text-field
                      id="lastName"
                      :label="formLabels('lastName')"
                      v-model="lastName"
                      :rules="nameRules"
                      filled
                      rounded
                      dense
                      required
                      color="rgb(123, 204, 231)"
                    ></v-text-field>
                  </v-col>
                  <!--</v-row>
                  <v-row>-->
                  <v-col cols="12" sm="6">
                    <v-text-field
                      id="contact"
                      :label="formLabels('contact')"
                      v-model="contact"
                      :rules="contactRules"
                      filled
                      rounded
                      dense
                      required
                      color="rgb(123, 204, 231)"
                    ></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="6">
                    <v-text-field
                      id="email"
                      :label="formLabels('email')"
                      v-model="email"
                      :rules="emailRules"
                      filled
                      rounded
                      dense
                      required
                      color="rgb(123, 204, 231)"
                    ></v-text-field>
                  </v-col>
                  <!--</v-row>
                  <v-row>-->
                  <v-col cols="8" md="6">
                    <v-menu
                      ref="menu"
                      v-model="menu"
                      :close-on-content-click="false"
                      :return-value.sync="date"
                      transition="scale-transition"
                      offset-y
                      min-width="290px"
                      color="rgb(123, 204, 231)"
                      id="menu"
                    >
                      <template v-slot:activator="{ on }">
                        <v-text-field
                          id="date1"
                          v-model="date"
                          :label="formLabels('date')"
                          v-on="on"
                          prepend-icon="mdi-calendar"
                          color="rgb(123, 204, 231)"
                        ></v-text-field>
                      </template>
                      <v-date-picker
                        v-model="date"
                        no-title
                        scrollable
                        color="rgb(123, 204, 231)"
                        :allowed-dates="allowedDates"
                        min="2020-05-20"
                        max="2021-12-31"
                      >
                        <v-spacer></v-spacer>
                        <v-btn text color="rgb(123, 204, 231)" @click="menu = false">Cancel</v-btn>
                        <v-btn text color="rgb(123, 204, 231)" @click="$refs.menu.save(date)">OK</v-btn>
                      </v-date-picker>
                    </v-menu>
                  </v-col>
                  <v-col cols="12" sm="6">
                    <v-textarea
                      id="about"
                      filled
                      auto-grow
                      :label="formLabels('problemDescription')"
                      v-model="about"
                      rows="4"
                      row-height="30"
                      shaped
                      color="rgb(123, 204, 231)"
                    ></v-textarea>
                  </v-col>
                </v-row>
              </v-container>
              <div class="row justify-content-center">{{$t("requiredField")}}</div>
              <div class="row justify-content-center">{{$t("infoAboutPDF")}}</div>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn
                  :disabled="!valid"
                  text
                  color="rgb(123, 204, 231)"
                  v-on:click="setAppointment"
                >{{$t("makeAppointment")}}</v-btn>
              </v-card-actions>
            </v-card-text>
          </v-form>
        </div>
      </div>
    </div>
  </v-app>
</template>

<script>
//import i18n from "@/i18n";
import jsPDF from "jspdf";
//import html2canvas from "html2canvas"
export default {
  name: "NMForm",
  data: () => ({
    name: "",
    lastName: "",
    nameRules: [v => !!v || "Ime/prezime je obavezno"],
    contactRules: [
      v => !!v || "Kontakt je obavezan",
      v =>
        /\(?([0-9]{3})\)?([ .-]?)([0-9]{3})\2([0-9]{4})/.test(v) ||
        "Kontakt mora da bude validan"
    ],
    contact: "",
    email: "",
    emailRules: [
      v => !!v || "E-mail je potreban",
      v => /.+@.+\..+/.test(v) || "E-mail mora da bude validan"
    ],
    date: new Date().toISOString().substr(0, 10),
    menu: false,
    about: "",
    valid: true,
    lazy: false
  }),

  methods: {
    allowedDates: val => parseInt(val.split("-")[2], 10) % 3 === 0,

    /*validate () {
        this.$refs.form.validate()
      },*/
    /*reset () {
        this.$refs.form.reset()
      },
      resetValidation () {
        this.$refs.form.resetValidation()
      },*/
    setAppointment() {
      this.$refs.form.validate();
      var doc = new jsPDF();
      var imgData =
        "data:image/png;base64,";
      doc.addImage(imgData, "PNG", 10, 7, 25, 20);

      let txt = this.$i18n.t("pdfHeader");
      doc.text(70, 20, txt);

      txt = this.$i18n.t("pdfFirstName");
      doc.text(23, 40, txt);
      doc.text(23, 50, this.name);

      txt = this.$i18n.t("pdfLastName");
      doc.text(23, 70, txt);
      doc.text(23, 80, this.lastName);

      txt = this.$i18n.t("pdfDate");
      doc.text(23, 100, txt);
      doc.text(23, 110, this.date);

      txt = this.$i18n.t("pdfContact");
      doc.text(23, 130, txt);
      doc.text(23, 140, this.contact);

      txt = this.$i18n.t("pdfEmail");
      doc.text(23, 160, txt);
      doc.text(23, 170, this.email);

      txt = this.$i18n.t("pdfProblemDesc");
      doc.text(23, 190, txt);
      doc.text(23, 200, this.about);

      txt = this.$i18n.t("pdfSeeYouSoon");
      doc.text(75, 250, txt);

      doc.setDrawColor(79, 79, 79);
      doc.line(18, 30, 180, 30);
      doc.line(18, 55, 180, 55);
      doc.line(18, 85, 180, 85);
      doc.line(18, 115, 180, 115);
      doc.line(18, 145, 180, 145);
      doc.line(18, 175, 180, 175);
      doc.line(18, 205, 180, 205);

      doc.save(this.name + this.lastName + ".pdf");
    },
    formLabels(descriptionKey) {
      let desc = this.$i18n.t(descriptionKey);
      return desc;
    }
  }
};
</script>

<style scoped>
#heading {
  text-align: center;
  text-decoration-line: underline;
  text-decoration-color: rgb(123, 204, 231);
  /*text-decoration-color:rgb(40, 122, 170);
  /*color:rgb(70, 167, 70);*/
}
.component-background {
  background-color: rgba(119, 136, 153, 0.2);
  border-radius: 16px;
}
#formHeading {
  padding-top: 20px;
  color: rgb(123, 204, 231);
  font-family: "Bevan", cursive;
}
#date1 {
  width: 150px;
}
#nesto {
  text-align: center;
}
</style>