export function setCookie(cname, cvalue) {
    let jsonedVal = JSON.stringify(cvalue);
    document.cookie = cname + "=" + jsonedVal + ";" + ";path=/";
}

export function getCookie(cname) {
    var name = cname + "=";
    var ca = document.cookie.split(';');
    for (var i = 0; i < ca.length; i++) {
        var c = ca[i];
        while (c.charAt(0) == ' ') {
            c = c.substring(1);
        }
        if (c.indexOf(name) == 0) {
            let cvalue = c.substring(name.length, c.length);
            return JSON.parse(cvalue);
        }
    }
    return null;
}